@import "mixins";



body,
h1
{

    font-family:Arial, Helvetica, sans-serif;
    margin:0;
    
}

div.Content{
      width:75%;
      min-width: 300px;
      margin:auto;
      border: 1px solid;
      min-height: 700px; 
}

  

nav {
    
    display:flex;
    flex-flow: row wrap;    

    min-height: 75px;    
    margin:auto;    
}

.current {
    color: green;
}

.header
{      
    background-color: #cecece; 
    margin:0px;
    min-height: 100px;        
    h1
    {
        padding: 10px 0 10px 10px;
    }

    
}

.navlink.current
{    
    background-color: rgb(100, 100, 100);
}

.navlink,
.button {        
    width: 115px;
    height: 25px;    
    padding: 10px;
    text-align: center;    
    color: white;
    font-weight: bold;
    line-height: 25px;        
    align-self: flex-end;
    text-decoration: none;
}

.navlink:hover{
    background-color: rgb(177, 177, 177);
}